"use client";

import { useQuery } from "@tanstack/react-query";
import { useEffect, useMemo, useState } from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
  DropdownMenuSeparator,
} from "@/components/ui/dropdown-menu";
import { MoreHorizontal, Pencil, Search, Trash2 } from "lucide-react";
import { client } from "@/helpers/api/client";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Input } from "@/components/ui/input";

// Define types
interface User {
  id: string;
  first_name: string;
  last_name: string;
  email: string;
  is_active: boolean;
  is_verified: boolean;
  roles: string[];
  created_at: string;
  updated_at: string;
}

interface UsersResponse {
  appointments: User[];
  total: number;
  page: number;
  size: number;
  pages: number;
  has_next: boolean;
  has_prev: boolean;
}

// Helper to format full name
const getFullName = (user: User) => `${user.first_name} ${user.last_name}`;

export default function UsersTableClient({ token }: { token?: string }) {
  const [page, setPage] = useState(1);
  const [size, setSize] = useState(20);
  const [search, setSearch] = useState("");
  const [roleFilter, setRoleFilter] = useState<string | null>(null);
  const [statusFilter, setStatusFilter] = useState<string | null>(null);
  const [verifiedFilter, setVerifiedFilter] = useState<string | null>(null);

  // ✅ Add debounced search HERE
  const debouncedSearch = useMemo(() => {
    const handler = setTimeout(() => {
      setPage(1); // reset to first page on new search
      refetch(); // trigger API refetch
    }, 400);
    return () => clearTimeout(handler);
  }, [search]);

  useEffect(() => {
    debouncedSearch();
  }, [search]);

  // Fetch users
  const { data, isLoading, error, refetch } = useQuery({
    queryKey: ["users", page, size, search, statusFilter, verifiedFilter],
    queryFn: async () => {
      if (!token) throw new Error("Unauthorized");

      const params: Record<string, any> = { page, size };

      if (search) params.search = search;
      if (statusFilter === "active") params.is_active = true;
      if (statusFilter === "inactive") params.is_active = false;
      if (verifiedFilter === "verified") params.is_verified = true;
      if (verifiedFilter === "unverified") params.is_verified = false;

      const response = await client.getUsers(token, params);
      return response as UsersResponse;
    },
  });

  if (isLoading) return <div>Loading...</div>;
  if (error) return <div>Error: {(error as Error).message}</div>;

  const users = data?.appointments || [];
  // Apply client-side role filter
  const filteredUsers = users.filter((user) => {
    if (!roleFilter) return true;
    return user.roles.includes(roleFilter);
  });
  console.log("d", data);
  console.log("U", users);

  const handleEdit = (id: string, role: string) => {
    console.log("Edit", id);
    console.log("Role", role);
  };

  return (
    <div className="">
      {/* Search bar and filters */}
      <div className="flex justify-between items-center my-3">
        <div className="relative w-full mr-4">
          <Search className="absolute left-4 top-1/2 transform -translate-y-1/2 h-4 w-4 text-brand-gray" />
          <Input
            value={search}
            onChange={(e) => {
              setSearch(e.target.value);
              setPage(1);
            }}
            placeholder="Search by name, email"
            className="pl-10 py-5 rounded-[4px] text-[16px]"
          />
        </div>
        {/* end search bar and start of filters */}
        <div className="flex gap-x-6">
          <div className="">
            <Select
              value={roleFilter || undefined}
              onValueChange={(value) => setRoleFilter(value)}
            >
              <SelectTrigger className="w-[180px] text-[16px] py-5 rounded-[4px] shadow-gren">
                <SelectValue placeholder="All Roles" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="admin">admin</SelectItem>
                <SelectItem value="host">host</SelectItem>
                <SelectItem value="reception">reception</SelectItem>
                <SelectItem value="secretary">secretary</SelectItem>
              </SelectContent>
            </Select>
          </div>
          <div className="">
            <Select
              value={statusFilter || undefined}
              onValueChange={(value) => {
                setStatusFilter(value);
                setPage(1);
              }}
            >
              <SelectTrigger className="w-[180px] text-[16px] py-5 rounded-[4px] shadow-gren">
                <SelectValue placeholder="All Status" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="active">active</SelectItem>
                <SelectItem value="inactive">inactive</SelectItem>
              </SelectContent>
            </Select>
          </div>
          <div>
            <Select
              value={verifiedFilter || undefined}
              onValueChange={(value) => {
                setVerifiedFilter(value);
                setPage(1);
              }}
            >
              <SelectTrigger className="w-[180px] text-[16px] py-5 rounded-[4px] shadow-gren">
                <SelectValue placeholder="Verified" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="verified">verified</SelectItem>
                <SelectItem value="unverified">unverified</SelectItem>
              </SelectContent>
            </Select>
          </div>
        </div>
      </div>

      {/* Table */}
      <div className="rounded-[4px] shadow-gren border border-[#eeeeee] overflow-hidden bg-white">
        <Table>
          <TableHeader>
            <TableRow className="bg-[#F5F5F5] border-b border-[#eeeeee]">
              <TableHead className="px-4 py-3 font-medium text-brand-gray">
                Name
              </TableHead>
              <TableHead className="px-4 py-3 font-medium text-brand-gray">
                Email
              </TableHead>
              <TableHead className="px-4 py-3 font-medium text-brand-gray">
                Status
              </TableHead>
              <TableHead className="px-4 py-3 font-medium text-brand-gray">
                Verified
              </TableHead>
              <TableHead className="px-4 py-3 font-medium text-brand-gray">
                Role
              </TableHead>
              <TableHead className="px-4 py-3 font-medium text-brand-gray">
                Actions
              </TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {filteredUsers.map((user) => (
              <TableRow
                key={user.id}
                className="border-t border-[#eeeeee] hover:bg-gray-50"
              >
                <TableCell className="px-4 py-3 font-medium">
                  {user.first_name} {user.last_name}
                </TableCell>
                <TableCell className="px-4 py-3">{user.email}</TableCell>
                <TableCell className="px-4 py-3">
                  <span className="px-2 py-1 bg-green-100 text-green-800 text-xs font-medium rounded-full">
                    active
                  </span>
                </TableCell>
                <TableCell className="px-4 py-3">
                  <span
                    className={`px-2 py-1 text-xs font-medium rounded-full ${
                      user.is_verified
                        ? "bg-blue-100 text-blue-800"
                        : "bg-red-100 text-red-800"
                    }`}
                  >
                    {user.is_verified ? "verified" : "unverified"}
                  </span>
                </TableCell>
                <TableCell className="px-4 py-3 lowercase">
                  {user.roles[0]}
                </TableCell>
                <TableCell className="px-4 py-3">
                  <div className="flex gap-2 items-center">
                    {/* Edit Button */}
                    {/* <button className="text-brand text-sm flex items-center gap-1 hover:text-green-600">
                      <Pencil className="h-4 w-4 text-green-500" />
                      Edit
                    </button> */}

                    {/* Delete Button */}
                    {/* <button className="text-red-600 text-sm flex items-center gap-1 hover:text-red-800">
                      <Trash2 className="h-4 w-4" />
                      Delete
                    </button> */}
                    <button
                      onClick={() => handleEdit(user.id, user.roles[0])}
                      className="text-brand text-sm flex items-center gap-1 hover:text-green-600 hover:bg-green-50 px-2 py-1 rounded"
                    >
                      <Pencil className="h-4 w-4 text-green-500" />
                      Edit
                    </button>

                    <button className="text-red-600 text-sm flex items-center gap-1 hover:text-red-800 hover:bg-red-50 px-2 py-1 rounded">
                      <Trash2 className="h-4 w-4" />
                      Delete
                    </button>
                    <DropdownMenu>
                      <DropdownMenuTrigger asChild>
                        <Button variant="ghost" className="h-8 w-8 p-0">
                          <MoreHorizontal className="h-4 w-4" />
                        </Button>
                      </DropdownMenuTrigger>
                      <DropdownMenuContent
                        align="end"
                        className="w-40 p-0 border border-[#eeeeee] rounded-md shadow-lg"
                      >
                        <DropdownMenuItem className="px-4 py-2 text-sm cursor-pointer text-green-600 bg-green-50 hover:bg-green-100 flex items-center gap-2">
                          Resend invite
                        </DropdownMenuItem>
                        <DropdownMenuItem className="px-4 py-2 text-sm cursor-pointer hover:bg-gray-50">
                          Activate user
                        </DropdownMenuItem>
                        <DropdownMenuItem className="px-4 py-2 text-sm cursor-pointer hover:bg-gray-50">
                          Deactivate user
                        </DropdownMenuItem>
                        <DropdownMenuItem className="px-4 py-2 text-sm cursor-pointer hover:bg-gray-50">
                          Suspend user
                        </DropdownMenuItem>
                        <DropdownMenuItem className="px-4 py-2 text-sm cursor-pointer hover:bg-gray-50">
                          Assign Role
                        </DropdownMenuItem>
                        <DropdownMenuItem className="px-4 py-2 text-sm cursor-pointer hover:bg-gray-50">
                          Revoke Role
                        </DropdownMenuItem>
                      </DropdownMenuContent>
                    </DropdownMenu>
                  </div>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>
    </div>
  );
}
